// jshint ignore: start

define('campaignManagement/campaigns/controllers/campaigns',['require','./../../module','tpl!./../new-campaign.html'],function (require) {
    'use strict';

    var app = require('./../../module');
    require('tpl!./../new-campaign.html');

    app.controller('campaignsCtrl', ['$scope', '$http', '$timeout', 'campaignsByStatus', 'navbarService', '$modal', function ($scope, $http, $timeout, $modal, campaignsByStatus, navbarService) {
        $scope.byStatus = [];
        $scope.openModal = openModal;

        function updateByStatus() {
            $scope.byStatus = campaignsByStatus.all();
        }

        campaignsByStatus.observe(updateByStatus, $scope);

        function openModal(size) {
            $modal.open({
                animation: 'true',
                templateUrl: 'campaignManagement/campaigns/new-campaign.html',
                controller: 'newCampaignCtrl',
                size: size
            });
		}
    }]);
});

